import React, { useState } from "react";
import { BrowserRouter } from "react-router-dom";
import { ConfigProvider } from "antd";
import { TabBar } from "antd-mobile";
import {
  AppOutline,
  AddOutline,
  UnorderedListOutline,
} from "antd-mobile-icons";
import HomeScreen from "./screens/HomeScreen";
import AddRecordScreen from "./screens/AddRecordScreen";
import RecordsListScreen from "./screens/RecordsListScreen";
import "./App.css";

const App = () => {
  const [activeTab, setActiveTab] = useState("home");

  const tabs = [
    {
      key: "home",
      title: "首页",
      icon: <AppOutline />,
    },
    {
      key: "add",
      title: "添加记录",
      icon: <AddOutline />,
    },
    {
      key: "records",
      title: "记录列表",
      icon: <UnorderedListOutline />,
    },
  ];

  const renderContent = () => {
    switch (activeTab) {
      case "home":
        return <HomeScreen onTabChange={setActiveTab} />;
      case "add":
        return <AddRecordScreen onTabChange={setActiveTab} />;
      case "records":
        return <RecordsListScreen onTabChange={setActiveTab} />;
      default:
        return <HomeScreen onTabChange={setActiveTab} />;
    }
  };

  return (
    <BrowserRouter basename="/car">
      <ConfigProvider>
        <div className="App">
          <div className="app-content">{renderContent()}</div>
          <TabBar
            activeKey={activeTab}
            onChange={(key: string) => setActiveTab(key)}
            className="tab-bar"
          >
            {tabs.map((item) => (
              <TabBar.Item key={item.key} icon={item.icon} title={item.title} />
            ))}
          </TabBar>
        </div>
      </ConfigProvider>
    </BrowserRouter>
  );
};

export default App;
