// 使用localStorage进行数据持久化存储
export interface FuelRecord {
  id?: number;
  date: string;
  mileage: number;
  fuelAmount: number;
  fuelPrice: number;
  totalCost: number;
}

export interface MaintenanceRecord {
  id?: number;
  date: string;
  content: string;
  unitPrice: number;
  quantity: number;
  totalCost: number;
}

export type RecordType = 'fuel' | 'maintenance';

class DatabaseManager {
  private fuelStorageKey = 'oilCal_fuel_records';
  private maintenanceStorageKey = 'oilCal_maintenance_records';
  private nextIdKey = 'oilCal_nextId';

  async initDatabase(): Promise<void> {
    // 初始化localStorage
    console.log('数据库初始化完成（localStorage模式）');
  }

  private getFuelRecords(): FuelRecord[] {
    const records = localStorage.getItem(this.fuelStorageKey);
    return records ? JSON.parse(records) : [];
  }

  private saveFuelRecords(records: FuelRecord[]): void {
    localStorage.setItem(this.fuelStorageKey, JSON.stringify(records));
  }

  private getMaintenanceRecords(): MaintenanceRecord[] {
    const records = localStorage.getItem(this.maintenanceStorageKey);
    return records ? JSON.parse(records) : [];
  }

  private saveMaintenanceRecords(records: MaintenanceRecord[]): void {
    localStorage.setItem(this.maintenanceStorageKey, JSON.stringify(records));
  }

  private getNextId(): number {
    const nextId = localStorage.getItem(this.nextIdKey);
    return nextId ? parseInt(nextId) : 1;
  }

  private setNextId(id: number): void {
    localStorage.setItem(this.nextIdKey, id.toString());
  }

  async addFuelRecord(record: Omit<FuelRecord, 'id'>): Promise<number> {
    const records = this.getFuelRecords();
    const nextId = this.getNextId();

    const newRecord: FuelRecord = {
      ...record,
      id: nextId,
    };

    records.unshift(newRecord); // 添加到开头，保持最新记录在前
    this.saveFuelRecords(records);
    this.setNextId(nextId + 1);

    console.log('加油记录已添加:', newRecord);
    return newRecord.id!;
  }

  async addMaintenanceRecord(record: Omit<MaintenanceRecord, 'id'>): Promise<number> {
    const records = this.getMaintenanceRecords();
    const nextId = this.getNextId();

    const newRecord: MaintenanceRecord = {
      ...record,
      id: nextId,
    };

    records.unshift(newRecord); // 添加到开头，保持最新记录在前
    this.saveMaintenanceRecords(records);
    this.setNextId(nextId + 1);

    console.log('保养记录已添加:', newRecord);
    return newRecord.id!;
  }

  async getAllFuelRecords(): Promise<FuelRecord[]> {
    return this.getFuelRecords();
  }

  async getAllMaintenanceRecords(): Promise<MaintenanceRecord[]> {
    return this.getMaintenanceRecords();
  }

  async getTotalCost(): Promise<number> {
    const fuelRecords = this.getFuelRecords();
    const maintenanceRecords = this.getMaintenanceRecords();
    const fuelTotal = fuelRecords.reduce((total, record) => total + record.totalCost, 0);
    const maintenanceTotal = maintenanceRecords.reduce((total, record) => total + record.totalCost, 0);
    return fuelTotal + maintenanceTotal;
  }

  async deleteFuelRecord(id: number): Promise<void> {
    const records = this.getFuelRecords();
    const filteredRecords = records.filter(record => record.id !== id);
    this.saveFuelRecords(filteredRecords);
    console.log('加油记录已删除:', id);
  }

  async deleteMaintenanceRecord(id: number): Promise<void> {
    const records = this.getMaintenanceRecords();
    const filteredRecords = records.filter(record => record.id !== id);
    this.saveMaintenanceRecords(filteredRecords);
    console.log('保养记录已删除:', id);
  }

  // 导出所有数据为JSON格式
  async exportData(): Promise<string> {
    const fuelRecords = this.getFuelRecords();
    const maintenanceRecords = this.getMaintenanceRecords();
    const totalCost = await this.getTotalCost();
    
    const exportData = {
      exportDate: new Date().toISOString(),
      totalCost,
      fuelRecords,
      maintenanceRecords,
      summary: {
        totalFuelRecords: fuelRecords.length,
        totalMaintenanceRecords: maintenanceRecords.length,
        totalRecords: fuelRecords.length + maintenanceRecords.length
      }
    };
    
    return JSON.stringify(exportData, null, 2);
  }

  // 下载JSON文件
  async downloadData(): Promise<void> {
    try {
      const jsonData = await this.exportData();
      const blob = new Blob([jsonData], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      
      const link = document.createElement('a');
      link.href = url;
      link.download = `vehicle-expenses-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
      
      console.log('数据导出成功');
    } catch (error) {
      console.error('数据导出失败:', error);
      throw error;
    }
  }

  // 导入JSON数据
  async importData(jsonData: string): Promise<{ success: boolean; message: string; importedCount: number }> {
    try {
      const importData = JSON.parse(jsonData);
      
      // 验证导入数据格式
      if (!importData.fuelRecords || !importData.maintenanceRecords) {
        throw new Error('无效的数据格式：缺少必要的记录字段');
      }

      let importedCount = 0;
      const errors: string[] = [];

      // 导入加油记录
      if (Array.isArray(importData.fuelRecords)) {
        for (const record of importData.fuelRecords) {
          try {
            // 验证记录格式
            if (record.date && record.mileage && record.fuelAmount && record.fuelPrice && record.totalCost) {
              // 检查是否已存在相同记录（基于日期、里程、加油量）
              const existingRecords = this.getFuelRecords();
              const isDuplicate = existingRecords.some(existing => 
                existing.date === record.date && 
                existing.mileage === record.mileage && 
                existing.fuelAmount === record.fuelAmount
              );

              if (!isDuplicate) {
                // 生成新的ID
                const nextId = this.getNextId();
                const newRecord: FuelRecord = {
                  ...record,
                  id: nextId
                };
                
                existingRecords.unshift(newRecord);
                this.saveFuelRecords(existingRecords);
                this.setNextId(nextId + 1);
                importedCount++;
              }
            }
          } catch (error) {
            errors.push(`加油记录导入失败: ${error}`);
          }
        }
      }

      // 导入保养记录
      if (Array.isArray(importData.maintenanceRecords)) {
        for (const record of importData.maintenanceRecords) {
          try {
            // 验证记录格式
            if (record.date && record.content && record.unitPrice && record.quantity && record.totalCost) {
              // 检查是否已存在相同记录（基于日期、内容和数量）
              const existingRecords = this.getMaintenanceRecords();
              const isDuplicate = existingRecords.some(existing => 
                existing.date === record.date && 
                existing.content === record.content && 
                existing.quantity === record.quantity
              );

              if (!isDuplicate) {
                // 生成新的ID
                const nextId = this.getNextId();
                const newRecord: MaintenanceRecord = {
                  ...record,
                  id: nextId
                };
                
                existingRecords.unshift(newRecord);
                this.saveMaintenanceRecords(existingRecords);
                this.setNextId(nextId + 1);
                importedCount++;
              }
            }
          } catch (error) {
            errors.push(`保养记录导入失败: ${error}`);
          }
        }
      }

      const message = errors.length > 0 
        ? `成功导入 ${importedCount} 条记录，${errors.length} 条记录导入失败`
        : `成功导入 ${importedCount} 条记录`;

      console.log('数据导入完成:', { importedCount, errors });
      
      return {
        success: true,
        message,
        importedCount
      };
    } catch (error) {
      console.error('数据导入失败:', error);
      return {
        success: false,
        message: `数据导入失败: ${error}`,
        importedCount: 0
      };
    }
  }

  // 处理文件上传
  async handleFileUpload(file: File): Promise<{ success: boolean; message: string; importedCount: number }> {
    return new Promise((resolve) => {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          const jsonData = e.target?.result as string;
          const result = await this.importData(jsonData);
          resolve(result);
        } catch (error) {
          resolve({
            success: false,
            message: `文件读取失败: ${error}`,
            importedCount: 0
          });
        }
      };
      
      reader.onerror = () => {
        resolve({
          success: false,
          message: '文件读取失败',
          importedCount: 0
        });
      };
      
      reader.readAsText(file);
    });
  }
}

export default new DatabaseManager();
